.header
{
    margin-top: 30px;
    margin-right: 100px;
    margin-left: 100px;
    margin-bottom: 30px;
    color: white;
    display: flex;
    flex-direction: column;
    row-gap: 50px;
}
h2
{
    color: rgb(255, 166, 0);
}
span
{
    color: rgb(173, 0, 165);
}
