}
.aboutdiv
{
    padding: 10px;
    width: 100%;
    height: max-content;
    box-shadow: 0 0 5px rgb(255, 166, 0), 0 0 10px rgb(255, 166, 0), 0 0 15px rgb(255, 166, 0);
}