.homediv
{
    display: flex;
    flex-direction: row;
    position: relative;
}
.textdiv
{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center; 
    margin-left: auto;
    height: 93vh; 
}


.hello
{
    color: white;
    font-size: 2rem;
    margin-left: 60px;
}
.name
{
    color: rgb(255, 166, 0);
    font-size: 4rem;
    display: inline-block;
}
.profession
{
    color: rgb(255, 255, 255);
    font-size: 1.5rem;
    background-color: rgb(255, 166, 0);
    border: solid 2px rgb(154, 100, 0);
    font-weight: bold;
    
}

.imagediv
{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 93vh;
    border-radius: 25px;
    position: relative;
}
.imagediv1 {
    border: solid 10px rgb(255, 166, 0);
    width: auto; 
    height: 65%; 
    overflow: hidden;
    border-radius: 50%;
    margin-right: 100px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.imagediv1 img {
    width: 100%;  
    height: auto; 
}

