.navbar
{
    height: 7vh;
    display: flex;
    position: relative;
    flex-direction: row;
    color: rgb(255, 255, 255);
    justify-content: space-around;
    font-size: 1.1rem;
    align-items: center;
    background-color: hsl(0, 0%, 4%);
}

.navbar p{
    font-size: 1.7em;
    color: rgb(255, 166, 0);
}
.unlist
{
    list-style-type: none;
}
.unlist li
{
    display: inline-block;
    margin-right: 20px;
}
.unlist li a:hover
{
    cursor: pointer;
    color: rgb(165, 221, 223);
    text-decoration: underline;
}

.unlist li a{
    text-decoration: none;
    color: white;
}
