.contactusform
{
    box-shadow: 0 0 15px rgb(255, 166, 0), 0 0 15px rgba(255, 166, 0, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px 200px 50px 200px;
    height: auto;
    row-gap: 20px;
    padding: 10px;
}
.contactusform h1
{
    color: rgb(255, 166, 0);
}
.contactusform input
{
    padding: 5px;
    width: 80%;
    margin: 5px;
    border: none;
    outline: none;
    border-bottom: solid 1px rgb(255, 166, 0);
    background: none;
    font-size: 20px;
    color: white;
}
.contactusform input::placeholder
{
    color: white;
}
.contactusform h3{
    color: white;
}
.contactusform textarea
{
    color: white;
    border: none;
    outline: none;
    background: none;
    border-bottom: solid 1px rgb(255, 166, 0);
    font-size: 20px;
    min-height: 50px;
    max-height: 50px;
    min-width: 450px;
    max-width: 450px;
    padding: 10px;
}
textarea ::-webkit-scrollbar
{
    width: 0.5rem;
}
.submit
{
    background-color: rgb(255, 166, 0);
    border-radius: 10px;
    width: 200px;
    height: 35px;
    font-size: 1.2rem;
    font-weight: bold;
    border: solid 2px black;
    color: white;
}
.submit:hover
{
    background-color: rgb(109, 72, 3);
}
.submit:active
{
    background-color: rgb(31, 236, 0);
}